import Vue from 'vue';
import { TabPane } from 'element-ui';
Vue.use(TabPane);
export default Vue.extend({
    functional: true,
    render(h, context) {
        return h('el-tab-pane', {
            props: context.props,
            on: context.data.on
        }, context.children);
    }
});



